import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home'
import Chat from '@/components/chat'

Vue.use(Router)

export default new Router({
	routes: [{
			path: '/',
			name: 'Home',
   			component: Home
		},
		{
			path: '/Chat',
			name: 'Chat',
			component: Chat
		}
	]
})



